import {useState} from 'react'
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import Button from "react-bootstrap/Button";
import Form from "react-bootstrap/Form";
import {convert} from "../Convert";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import {faCopy, faBolt} from '@fortawesome/free-solid-svg-icons';

export function LayoutForm() {

    const [value, setValue] = useState('');
    const [valueDecoded, setValueDecoded] = useState('');
    const [valueSelectEnc, setValueSelectEnc] = useState('auto');
    const [valueSelectDec, setValueSelectDec] = useState('ru');

    function decode(value: string) {
        setValueDecoded(convert(value, valueSelectEnc, valueSelectDec));
    }

    const changeHandler = (event: React.ChangeEvent<HTMLTextAreaElement>) => {
        setValue(event.target.value);
        decode(event.target.value);
    }

    const changeHandler2 = (event: React.ChangeEvent<HTMLTextAreaElement>) => {
        setValueDecoded(event.target.value);
    }

    function handleSelect(event: React.FormEvent<HTMLSelectElement>) {
        setValueSelectEnc(event.currentTarget.value);
    }

    function handleClick() {
        decode(value);
    }

    function clearForms() {
        setValue('');
        setValueDecoded('');
    }

    const langs = {
        'ru': '',
        'en': 'English ()',
        'uk': ' ()',
        'be': ' ()',
        'uz': ' ()',
        'kk': ' ()',
        'az': 'Azrbaycanca ()',
        'lt': 'Lietuvi ()',
        'mo': 'Moldoveneasc ()',
        'lv': 'Latvieu ()',
        'ky': ' ()',
        'tg': ' ()',
        'hy': ' ()',
        'tk': 'Trkmen ()',
        'et': 'Eesti ()'
    }

    return (
        <Form className="mb-3" id="convert-form">
            <Row>
                <Col sm={6}>
                    <fieldset className="mb-3">
                        <label className="form-label" htmlFor="coded"> </label>
                        <textarea
                            id="coded"
                            onChange={changeHandler}
                            value={value}
                            className="form-control"
                            cols={50}
                            rows={12}
                            autoComplete="off" autoCorrect="off" autoCapitalize="off"
                            spellCheck="false"
                            placeholder="     "
                        />
                    </fieldset>
                </Col>
                <Col sm={6}>
                    <fieldset className="mb-3">
                        <label className="form-label" htmlFor="decoded"> </label>
                        <textarea
                            id="decoded"
                            onChange={changeHandler2}
                            value={valueDecoded}
                            className="form-control"
                            cols={50}
                            rows={12}
                        />
                    </fieldset>
                </Col>
            </Row>
            <Row>
                <Col md={6}>
                    <fieldset className="mb-3">
                        <select
                            value={valueSelectEnc}
                            onChange={handleSelect}
                            className="form-select"
                            aria-label=" "
                            >
                            <option className="lang-sm lang-lbl-full" value="auto">
                            </option>
                            { Object.keys(langs).map((lang) =>
                                <option key={lang} value={lang}>{langs[lang as keyof typeof langs]}</option>
                            ) }
                        </select>
                    </fieldset>
                </Col>
                <Col md={6}>
                    <fieldset className="mb-3">
                        <select
                            value={valueSelectDec}
                            onChange={(event) => setValueSelectDec(event.target.value)}
                            className="form-select"
                            aria-label="   "
                            >
                            { Object.keys(langs).map((lang) =>
                                <option key={lang} value={lang}>{langs[lang as keyof typeof langs]}</option>
                            ) }
                        </select>
                    </fieldset>
                    <fieldset className="fieldset_wide">
                        <div className="mb-3">
                            <Button
                                onClick={handleClick}
                                variant="info"
                                type="button"
                                className="me-2"
                                >
                                <FontAwesomeIcon icon={faBolt} title="start" /> !</Button>
                        </div>
                        <div className="mb-3">
                            <Button
                                onClick={() => {navigator.clipboard.writeText(valueDecoded)}}
                                variant="light"
                                type="button"
                                className="me-2"
                                >
                                <FontAwesomeIcon icon={faCopy} title="copy" /> 
                            </Button>
                            <Button variant="light" onClick={clearForms}>
                                
                            </Button>
                        </div>
                    </fieldset>
                </Col>
            </Row>
        </Form>
    )
}