import React from 'react'
import {Helmet} from "react-helmet";
import {DocsHeader} from "../components/DocsHeader";
import Container from "react-bootstrap/Container";
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import {faLongArrowAltRight} from '@fortawesome/free-solid-svg-icons';
import {LayoutForm} from "../components/LayoutForm";
import {Features} from '../components/Features';
import { LayoutKeyboard } from '../components/LayoutKeyboard';

export function MainPage() {

    const langs = {
        'ru': '',
        'en': 'English ()',
        'uk': ' ()',
        'be': ' ()',
        'uz': ' ()',
        'kk': ' ()',
        'az': 'Azrbaycanca ()',
        'lt': 'Lietuvi ()',
        'mo': 'Moldoveneasc ()',
        'lv': 'Latvieu ()',
        'ky': ' ()',
        'tg': ' ()',
        'hy': ' ()',
        'tk': 'Trkmen ()',
        'et': 'Eesti ()'
    }

    const featuresList = [
        {title: '', iconName: 'shield', desc: '     ,     '},
        {title: '', iconName: 'flash', desc: '    '},
        {title: '', iconName: 'star', desc: '      ,      '},
    ]

    const seoData = {
        title: '   :     (16 )',
        description: '        :      .',
        site_name: 'Keybd.ru'
    }

    return (
        <div>
            <Helmet>
                <title>{seoData.title}</title>
                <link rel="canonical" href="https://keybd.ru/" />
                <meta name="description" content={seoData.description} />
                <meta property="og:locale" content="ru_RU" />
                <meta property="og:type" content="website" />
                <meta property="og:title" content={seoData.title} />
                <meta property="og:description" content={seoData.description} />
                <meta property="og:url" content="https://keybd.ru/" />
                <meta property="og:site_name" content={seoData.site_name} />
                <meta property="og:image" content="/logo512.png"></meta>
                <meta property="og:image:type" content="image/png" />
            </Helmet>

            <DocsHeader
                title="   "
                lead={
                    <>
                        { Object.keys(langs).map((lang, index) => <span key={lang} className={'flag flag-' + lang} title={langs[lang as keyof typeof langs]}></span>) }
                    </>
                }
            />

            <main>

                <Container>

                    <div className="intro">
                        <div className="sr-only">
                            <span className="font-bold">:</span>
                        </div>
                        <p>
                            <span className="font-bold">Ghbdtn! </span><FontAwesomeIcon icon={faLongArrowAltRight} /><span className="font-bold"> ! </span>
                            <span className="font-bold"> ! </span><FontAwesomeIcon icon={faLongArrowAltRight} /><span className="font-bold"> Hello! </span>
                        </p>
                    </div>
                    <br/>

                    <LayoutForm></LayoutForm>

                    <h2 className="sr-only">  </h2>
                    <LayoutKeyboard></LayoutKeyboard>

                    <Features list={featuresList}></Features>

                    <div className="well panel-narrow">
                        <Row>
                            <Col md={12}>
                                <div className="small">
                                    <p>    
                                     ,     ,   <strong> 
                                    </strong>,     .      
                                    ,        .     
                                        ,       .</p>
                                    <p>       .        
                                        .     
                                     .    .     <strong>   </strong>.
                                    </p>
                                    <p><strong>  ():</strong> , , , , , , , , , , , , , , .   .</p>
                                </div>
                            </Col>
                        </Row>
                    </div>

                </Container>

            </main>
        </div>
    )
}