import React from 'react'
import {Helmet} from "react-helmet";
import {DocsHeader} from "../components/DocsHeader";
import Container from "react-bootstrap/Container";
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import {faLongArrowAltRight} from '@fortawesome/free-solid-svg-icons';
import {TypografForm} from "../components/TypografForm";
import {Features} from '../components/Features';

export function TypografPage() {

    const featuresList = [
        {title: '', iconName: 'shield', desc: '     ,     '},
        {title: '', iconName: 'flash', desc: '    '},
        {title: '', iconName: 'star', desc: '             '},
    ]

    const seoData = {
        title: ' :       -',
        description: '    ,   -,     , ,  ,   .',
        site_name: 'Keybd.ru'
    }

    return (
        <div>
            <Helmet>
                <title>{seoData.title}</title>
                <link rel="canonical" href="https://keybd.ru/text-case" />
                <meta name="description" content={seoData.description} />
                <meta property="og:locale" content="ru_RU" />
                <meta property="og:type" content="website" />
                <meta property="og:title" content={seoData.title} />
                <meta property="og:description" content={seoData.description} />
                <meta property="og:url" content="https://keybd.ru/text-case" />
                <meta property="og:site_name" content={seoData.site_name} />
                <meta property="og:image" content="/logo512.png"></meta>
                <meta property="og:image:type" content="image/png" />
            </Helmet>

            <DocsHeader
                title=""
                lead={
                    <>
                         ,    
                    </>
                }
            />

            <main>

                <Container>

                    <div className="intro">
                        <div className="sr-only">
                            <span className="font-bold">:</span>
                        </div>
                        <p>
                            <span className="font-bold">",  ?", -  . </span><FontAwesomeIcon icon={faLongArrowAltRight} /><span className="font-bold"> ,  ?,   .</span>
                        </p>
                    </div>
                    <br/>

                    <TypografForm></TypografForm>

                    <Features list={featuresList}></Features>

                    <div className="well panel-narrow">
                        <Row>
                            <Col md={12}>
                                <small>
                                    <p> <strong></strong>.        .    <strong> -</strong>, <strong>   </strong>, <strong>  , ,     </strong>.      .     <strong> </strong>, <strong> </strong>, <strong></strong>  <strong> , , , </strong>   .
                                    </p>
                                    <p>          .          .</p>
                                </small>
                            </Col>
                        </Row>
                    </div>

                </Container>

            </main>
        </div>
    )
}